.nav {
    position: fixed;
    top: 0;
    padding: 10px;
    width: 100%;
    height: 40px;
    z-index: 1;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__contents {
    display: flex;
    justify-content: space-between;
}

.nav__black {
    background-color: #111;
}

.nav__logo {
    position: fixed;
    left: 0;
    top: 15px;
    width: 120px;
    cursor: pointer;
    object-fit: contain;
    padding-left: 20px;
    fill: #e50914;
    line-height: normal;
}

.nav__avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}